/*                                                      <STOP-SCROLL>                                                                                                                                                            */
/*                                                      <BURGER>                                                                                                                                                            */
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*, *::before, *::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

aside, nav, footer, header, section {
  display: block;
}

html, body {
  position: relative;
  min-width: 320px;
}

html {
  box-sizing: border-box;
}

body {
  height: 100%;
  overflow-x: hidden;
  line-height: 1;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
  color: inherit;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

ul, ol, li, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

button:focus, input:focus {
  outline: transparent;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #F5F5F5;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.row {
  display: flex;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  z-index: 2;
  position: relative;
}

.header__container {
  max-width: 1220px;
  margin-right: auto;
  margin-left: auto;
}

.header__wrapper {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  max-width: 200px;
}

.header__logo img {
  width: 100%;
}

.header__content {
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
  height: 100px;
}

.content__card {
  color: #000000;
}

.content__card .card__title {
  margin-bottom: 11px;
  padding-bottom: 10px;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.21;
  text-transform: uppercase;
  max-width: 300px;
  border-bottom: 2px solid #DB0000;
}

.content__card .card__text {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}

.content__social {
  display: flex;
}

.content__social .social__wrapper {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.content__social .social__wrapper:last-child {
  margin-right: 0;
}

.content__social .social__wrapper .social__img {
  width: 100%;
  margin-right: 10px;
}

.content__social .social__wrapper .social__img img {
  max-width: 100%;
}

.content__social .social__wrapper .social__img:hover {
  opacity: 0.4;
}

.content__file .file__wrapper {
  display: flex;
}

.content__file .file__img {
  max-width: 100%;
  margin-right: 21px;
}

.content__file .file__img img {
  width: 100%;
}

.content__file .file__img:hover {
  opacity: 0.4;
}

.content__file .file__text {
  max-width: 100px;
  text-decoration: none;
  font-weight: 400;
  font-size: 15px;
  display: flex;
  align-items: center;
  line-height: 1.2;
  color: #292F36;
}

.content__file .file__text:hover {
  text-decoration: underline;
}

.header__content--wrapper {
  width: 75%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__nav {
  display: flex;
  background-color: #DBDBDB;
  margin-top: 30px;
}

.header__nav .nav {
  width: 100%;
}

.header__nav .nav-ul {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header__nav .nav-ul li {
  max-width: 150px;
  text-align: center;
}

.header__nav .nav__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #000000;
}

.header__nav .nav__link:focus, .header__nav .nav__link:hover, .header__nav .nav__link:active {
  text-decoration: none;
  color: #BF0000;
  font-weight: 700;
  transition: all 0.2s ease;
}

.header__mobile--nav {
  display: none;
}

.menu-active {
  display: block;
  transition: all 0.5s ease;
}

.menu-burger__header {
  display: none;
}

@media (max-width: 1140px) {
  .content__card .card__title {
    font-size: 16px;
    max-width: 251px;
  }
  .content__card .card__text {
    font-size: 10px;
  }
  .content__social .social__wrapper {
    margin-right: 10px;
  }
}

@media (max-width: 1060px) {
  .content__card .card__title {
    font-size: 14px;
    max-width: 220px;
  }
  .content__card .card__text {
    font-size: 10px;
  }
  .content__social .social__wrapper {
    margin-right: 8px;
  }
  .header__nav .nav__link {
    font-size: 16px;
  }
}

@media (max-width: 990px) {
  .menu-burger__header {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    width: 60px;
    height: 65px;
    padding: 5px 5px;
    margin-top: 5%;
    margin-right: 30px;
  }
  .menu-burger__header span, .menu-burger__header:after, .menu-burger__header:before {
    height: 3px;
    width: 40px;
    position: absolute;
    background: #515758;
    z-index: 10;
    margin: 0 auto;
    top: 35px;
  }
  .menu-burger__header span {
    top: 21px;
  }
  .menu-burger__header:after, .menu-burger__header:before {
    content: '';
  }
  .menu-burger__header:after {
    bottom: 5px;
  }
  .menu-burger__header:before {
    top: 5px;
  }
  .open-menu {
    z-index: 11;
    margin-top: 0;
    top: -35px;
  }
  .menu-burger__header.open-menu span {
    opacity: 0;
    transition: 0.5s;
  }
  .menu-burger__header.open-menu:before {
    transform: rotate(40deg);
    top: 35px;
    transition: 0.4s;
  }
  .menu-burger__header.open-menu:after {
    transform: rotate(-40deg);
    bottom: 10px;
    transition: 0.4s;
  }
  .content__card {
    display: none;
  }
  .content__social .social__wrapper {
    display: none;
  }
  .content__file {
    display: none;
  }
  .header__nav .nav {
    display: none;
  }
  .header__mobile--nav .mobile__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #DBDBDB;
    width: 100vw;
    height: 100%;
    z-index: 10;
    position: fixed;
    left: 0;
    top: 0;
    transition: all 0.5s;
    opacity: .95;
  }
  .header__mobile--nav .mobile__nav-ul {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .header__mobile--nav .mobile__nav__link {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    color: #000000;
    margin-top: 20px;
  }
  .header__mobile--nav .mobile__nav__link:focus, .header__mobile--nav .mobile__nav__link:hover, .header__mobile--nav .mobile__nav__link:active {
    text-decoration: none;
    color: #BF0000;
    font-weight: 700;
    transition: all 0.2s ease;
  }
  .header__mobile--nav .mobile__social--wrapper {
    margin-bottom: 150px;
    z-index: 10;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  }
  .header__mobile--nav .mobile__social--wrapper .social__img {
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .header__mobile--nav .mobile__social--wrapper .social__img:hover {
    opacity: 0.6;
  }
  .header__mobile--nav .mobile__file--wrapper {
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
  }
  .header__mobile--nav .mobile__file--wrapper .file__img:hover {
    opacity: 0.6;
  }
  .header__mobile--nav .mobile__file--wrapper .file__text {
    margin-top: 10px;
    max-width: 120px;
    text-align: center;
  }
}

.info {
  background-image: url("../img/bg/info__bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -60px;
  position: relative;
}

.info__wrapper {
  margin-top: 150px;
}

.info__wrapper .info__title--block {
  max-width: 890px;
  background: rgba(0, 0, 0, 0.55);
  background-blend-mode: multiply;
}

.info__wrapper .info__title--block .info__title {
  padding: 20px 40px 20px 20px;
  font-weight: 900;
  font-size: 66px;
  line-height: 1.21;
  text-transform: uppercase;
  color: #FFFFFF;
}

.info__wrapper .info__select {
  margin-bottom: 45px;
}

.info__select {
  position: relative;
  width: 280px;
  height: 40px;
  margin: 0 auto;
}

.info__select[data-state="active"] .info__select__title::before {
  transform: translate(-5px, -50%) rotate(-45deg);
}

.info__select[data-state="active"] .info__select__title::after {
  transform: translate(5px, -50%) rotate(45deg);
}

.info__select[data-state="active"] .info__select__content {
  opacity: 1;
}

.info__select[data-state="active"] .info__select__label + .info__select__input + .info__select__label {
  max-height: 40px;
  border-top-width: 1px;
}

.info__select__title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.21;
  color: #FFFFFF;
  padding: 8px 16px;
  background: #BF0000;
  border-radius: 6px 6px 0px 0px;
  cursor: pointer;
}

.info__select__title::before, .info__select__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 16px;
  display: block;
  width: 15px;
  height: 2px;
  transition: all 0.3s ease-out;
  background-color: #FFFFFF;
  transform: translate(-5px, -50%) rotate(45deg);
}

.info__select__title::after {
  transform: translate(5px, -50%) rotate(-45deg);
}

.info__select__title:hover::before, .info__select__title:hover::after {
  background-color: #FFFFFF;
}

.info__select__content {
  position: absolute;
  top: 40px;
  left: 3px;
  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);
  background-color: #ffffff;
  border: 1px solid #c7ccd1;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  opacity: 0;
  z-index: 8;
}

.info__select__input {
  display: none;
}

.info__select__input:checked + label {
  background-color: #dedede;
}

.info__select__input:disabled + label {
  opacity: 0.6;
  pointer-events: none;
}

.info__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 16px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;
}

.info__select__label + input + .info__select__label {
  border-top: 0 solid #C7CCD160;
}

.info__select__label:hover {
  background-color: #BF0000 !important;
  color: #ffffff;
}

.info__btn--wrapper {
  margin-top: 300px;
  margin-bottom: 115px;
}

.info__btn {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.22;
  color: #FFFFFF;
  text-align: center;
  padding: 20px 40px;
  cursor: pointer;
  background-color: #B50000;
}

.info__btn:hover {
  color: #B50000;
  background-color: #FFFFFF;
  transition: all 0.4s ease;
}

.info__img-car {
  top: -260px;
  position: absolute;
}

@media (max-width: 1380px) {
  .info__title {
    font-size: 55px !important;
  }
}

@media (max-width: 990px) {
  .info__title {
    font-size: 45px !important;
  }
  .car__img--wrapper {
    display: none;
  }
  .btn__wrapper {
    display: flex !important;
    justify-content: center !important;
    width: 100% !important;
  }
  .info__btn {
    width: 400px;
  }
}

@media (max-width: 768px) {
  .info__title {
    font-size: 35px !important;
    text-align: center;
  }
  .info__select {
    margin-top: 50px;
    width: 400px;
    height: 50px;
  }
  .info__btn--wrapper {
    margin-top: 150px;
  }
  .btn__wrapper {
    display: flex !important;
    justify-content: center !important;
    width: 100% !important;
  }
  .info__btn {
    width: 400px;
  }
}

@media (max-width: 576px) {
  .info__title {
    font-weight: 700;
  }
  .info__title--block {
    display: flex;
    justify-content: center;
    width: 80% !important;
  }
  .info__select {
    margin-top: 50px;
    width: 400px;
    height: 50px;
  }
  .info__btn--wrapper {
    margin-top: 150px;
  }
  .btn__wrapper {
    display: flex !important;
    justify-content: center !important;
    width: 100% !important;
  }
  .info__btn {
    width: 400px;
  }
}
